import React from "react"
import {Table,Button} from "react-bootstrap"
import * as portfolioStyles from "./portfolio.module.css"
import { StaticImage } from "gatsby-plugin-image";

export default class Portfolio extends React.Component {
    constructor(props) {
        super(props);
    }
    goToPage(sPage){

    }
    render(){
        return (
            <div className="mainBody">
                <div className={portfolioStyles.cl_table}>
                    <div>
                        <Table bordered> 
                            <thead>
                                <tr>
                                    <th>Name</th>
                                    <th>Source Code</th>
                                    <th>Technologies</th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tr>
                                <td>Info Countries by Region</td>
                                <td>
                                    <a href="https://github.com/lair60/ngrx" target="_blank">Github code</a>
                                </td>
                                <td>
                                    NgRx
                                    <StaticImage src="../images/ngrx.svg" alt="Ngrx Icon" layout="fixed" height={28} width={28} style={{display:"inline-block",verticalAlign:"middle"}}/>                                    
                                </td>
                                <td>
                                    <Button variant="success" size="sm" href="https://stackblitz.com/github/lair60/ngrx" onClick={() => {return this.goToPage('ngrx');}} target="_blank">View</Button>                                    
                                </td>
                            </tr>
                            <tr>
                                <td>Tour of Heroes Tutorial</td>
                                <td><a href="https://github.com/lair60/tour-of-heroes" target="_blank">Github code</a></td>
                                <td>Angular<StaticImage src="../images/angular.png" alt="Angular Icon" layout="fixed" height={28} width={28} style={{display:"inline-block",verticalAlign:"middle"}}/></td>
                                <td>
                                    <a className="btn btn-success btn-xs" href="https://stackblitz.com/edit/angular-tour-of-heroes-lair60" onClick={() => {return this.goToPage('angularTutorial');}} target="_blank">View</a></td>
                            </tr>
                            <tr>
                                <td>Country Flags Quiz</td>
                                <td>N/A</td>
                                <td>
                                    Java<StaticImage src="../images/iconfinder_java_logo.png" alt="Java Icon" layout="fixed" height={28} width={28} style={{display:"inline-block",verticalAlign:"middle"}}/>, Google Play<StaticImage src="../images/iconfinder_google_play.png" alt="GooglePlay Icon" layout="fixed" height={28} width={28} style={{display:"inline-block",verticalAlign:"middle"}}/>
                                </td>
                                <td>
                                    <a className="btn btn-success btn-xs" href="https://play.google.com/store/apps/details?id=com.luisingarivera.countryflagsquiz" onClick={ () => {return this.goToPage('googlePlay');}} target="_blank">
                                        View
                                    </a>
                                </td>
                            </tr>
                            <tr>
                                <td>Mini Blog</td>
                                <td><a href="https://github.com/lair60/diyblog" target="_blank">Github code</a></td>
                                <td>
                                    <StaticImage src="../images/django-logo-negative.png" alt="Django Icon" layout="fixed" height={28} style={{display:"inline-block",verticalAlign:"middle"}}/> ,
                                    <StaticImage src="../images/heroku-logotype-horizontal-purple.png" alt="Heroku Icon" layout="fixed" height={28} style={{display:"inline-block",verticalAlign:"middle"}}/>                                    
                                </td>
                                <td>
                                    <a className="btn btn-success btn-xs"  href="https://django-mini-blog.herokuapp.com/" onClick={ () => {return this.goToPage('djangoMiniBlog');}} target="_blank">
                                        View
                                    </a>
                                </td>
                            </tr>
                            <tr>
                                <td>Local Library</td>
                                <td><a href="https://github.com/lair60/django_local_library" target="_blank">Github code</a></td>
                                <td>
                                    <StaticImage src="../images/django-logo-negative.png" alt="Django Icon" layout="fixed" height={28} style={{display:"inline-block",verticalAlign:"middle"}}/> ,
                                    <StaticImage src="../images/heroku-logotype-horizontal-purple.png" alt="Heroku Icon" layout="fixed" height={28} style={{display:"inline-block",verticalAlign:"middle"}}/>                                    
                                </td>
                                <td>
                                    <a className="btn btn-success btn-xs" href="https://local-library-django.herokuapp.com/" onClick={ () => {return this.goToPage('djangoLibrary');}} target="_blank">
                                        View
                                    </a>
                                </td>
                            </tr>
                            <tr>
                                <td>React + UI5</td>
                                <td><a href="https://github.com/lair60/react-ui5" target="_blank">Github code</a></td>
                                <td>
                                    <StaticImage src="https://www.docker.com/sites/default/files/d8/2019-07/horizontal-logo-monochromatic-white.png" alt="Docker Icon" layout="fixed" height={28} style={{display:"inline-block",verticalAlign:"middle"}}/> ,
                                    <StaticImage src="https://raw.githubusercontent.com/SAP/ui5-webcomponents-react/master/assets/Logo.png" alt="UI5 React Icon" layout="fixed" height={28} style={{display:"inline-block",verticalAlign:"middle"}}/> ,
                                    <StaticImage src="../images/heroku-logotype-horizontal-purple.png" alt="Heroku Icon" layout="fixed" height={28} style={{display:"inline-block",verticalAlign:"middle"}}/>                                    
                                </td>
                                <td>
                                    <a className="btn btn-success btn-xs" href="https://docker-react-ui5.herokuapp.com/" onClick={() => {return this.goToPage('react-ui5');}} target="_blank">
                                        View
                                    </a>
                                </td>
                            </tr>
                        </Table>
	                </div>
                </div>
            </div>
        )
    }
}